import { List } from '@material-ui/core';
import { useState } from 'react'

import UnitListItem from "./UnitListItem";
import { makeStyles } from '@material-ui/core/styles';
import Pagination from '@material-ui/lab/Pagination';

import { ListItem, Divider, ListItemText, ListItemAvatar, Avatar, Typography, ListItemSecondaryAction, IconButton, ButtonBase } from '@material-ui/core';

const useStyles = makeStyles((theme) => ({
    root: {
      width: '100%',
      backgroundColor: theme.palette.background.paper,
    },
    inline: {
      display: 'inline',
    },
  }));

const UnitList = ({units, onToggle, page, numOfPages, itemsPerPage, handlePaging}) => {
    const classes = useStyles();

    return (
        <div style={{display:'flex', flexDirection:'column', justifyContent:'center'}}>
        <List className={classes.root}>
            {units.slice((page - 1) * itemsPerPage, page * itemsPerPage).map((unit) => (<UnitListItem key={unit._id} unit={unit} onToggle={onToggle} />))}
            {/* {units.map((unit) => (
                <UnitListItem key={unit.id} unit={unit} onToggle={onToggle} />
            ))} */}
        </List>
        <Pagination count={numOfPages} page={page} onChange={handlePaging} showFirstButton showLastButton style={{display:'flex', justifyContent:'center'}} />
        </div>
    )
}

export default UnitList
