import React from 'react';
import { useState, useEffect } from 'react'

import {Button, Typography, Divider, RadioGroup, Slider } from '@material-ui/core';

import Accordion from '@material-ui/core/Accordion';
import AccordionDetails from '@material-ui/core/AccordionDetails';
import AccordionSummary from '@material-ui/core/AccordionSummary';
import ExpandMoreIcon from '@material-ui/icons/ExpandMore';

import { makeStyles } from '@material-ui/core/styles';
import UnitList from "../components/UnitList";
import Spinner from '../components/Spinner'
import RadioFilter from '../components/RadioFilter'


const useStyles = makeStyles((theme) => ({
    root: {
      //width: '100%',
      display: 'flex',
      flexDirection: 'row',
      backgroundColor: theme.palette.background.paper,
      marginLeft:'72px',
      marginTop:'64px',
      paddingTop:'5px',
      paddingLeft:'5px',
      paddingRight:'5px'
    },
    inline: {
      display: 'inline',
    },
    heading: {
      fontSize: theme.typography.pxToRem(15),
      flexBasis: '33.33%',
      flexShrink: 0,
    },
    secondaryHeading: {
      fontSize: theme.typography.pxToRem(15),
      color: theme.palette.text.secondary,
      width: '100%'
    },
  }));

const Search = ({units, onToggle, page, numOfPages, itemsPerPage, handlePaging, isLoading}) => {
    const [types, setTypes] = useState([]);
    const [crewVal, setCrewVal] = useState([0, 1000]);
    const [accordion, setAccordion] = useState(false);
    const [typeRadio, setTypeRadio] = useState({id: 0, value: 'All'});
    const [classRadio, setClassRadio] = useState({id: 0, value: 'All'});

    useEffect(() => {
      const getTypes = async () =>{
        const typesFromServer = await fetchTypes()
        setTypes(typesFromServer)
      }
      getTypes()
    }, []);
    const fetchTypes = async () => {
      const res = await fetch('http://localhost:5000/unitTypes');
      const data = await res.json();

      return data;
    }

    const resetSearch = () => {
      setCrewVal([0, 1000]);
      setTypeRadio({id: 0, value: 'All'});
      setClassRadio({id: 0, value: 'All'});
    }

    function sliderValuetext(value) {
      return value;
    }
    const sliderChange = (event, value) => {
      setCrewVal(value);
    }
    const toggleAccordion = (panel) => (event, isExpanded) => {
      setAccordion(isExpanded ? panel : false);
    }
    const typeRadioChange = (event) => {
      setTypeRadio({id: event.target.id, value: event.target.value});
    }
    const classRadioChange = (event) => {
      setClassRadio({id: event.target.id, value: event.target.value});
    }

    const classes = useStyles();

    return (
        <div className={classes.root}>
          <div style={{width:'100%'}}>
            {isLoading ? <Spinner /> : <UnitList units={units} onToggle={onToggle} page={page} numOfPages={numOfPages} itemsPerPage={itemsPerPage} handlePaging={handlePaging} />}
          </div>
          <div style={{width:'400px', display:'flex', flexDirection:'column', textAlign:'center', justifyContent:'space-between'}}>
            <div>
              <Typography variant='h5'>Filters</Typography>
              <Divider variant='middle' />

              <div style={{padding:'5%', overflowY:'scroll', height:'80vh'}}>
                <Accordion expanded={accordion === 'panel1'} onChange={toggleAccordion('panel1')}>
                  <AccordionSummary expandIcon={<ExpandMoreIcon />}>
                    <Typography className={classes.heading}>Type</Typography>
                    <Typography className={classes.secondaryHeading}>{typeRadio.value}</Typography>
                  </AccordionSummary>
                  <AccordionDetails>
                    <RadioGroup onChange={typeRadioChange}>
                      {types.map((type) => (<RadioFilter key={type._id} value={type.name} checked={{id:type._id, checkedId:typeRadio.id}} />))}
                    </RadioGroup>
                  </AccordionDetails>
                </Accordion>

                <Accordion expanded={accordion === 'panel2'} onChange={toggleAccordion('panel2')}>
                  <AccordionSummary expandIcon={<ExpandMoreIcon />}>
                    <Typography className={classes.heading}>Class</Typography>
                    <Typography className={classes.secondaryHeading}>{classRadio.value}</Typography>
                  </AccordionSummary>
                  <AccordionDetails>
                    <RadioGroup onChange={classRadioChange}>
                      {types.map((type) => (<RadioFilter key={type._id} value={type.name} checked={{id:type._id, checkedId:classRadio.id}} />))}
                    </RadioGroup>
                  </AccordionDetails>
                </Accordion>

                <Accordion expanded={accordion === 'panel3'} onChange={toggleAccordion('panel3')}>
                  <AccordionSummary expandIcon={<ExpandMoreIcon />}>
                    <Typography className={classes.heading}>Crew</Typography>
                    <Typography className={classes.secondaryHeading}>{crewVal.toString().replace(',', ' - ')}</Typography>
                  </AccordionSummary>
                  <AccordionDetails>
                    <Slider
                        value={crewVal}
                        onChange={sliderChange}
                        valueLabelDisplay="auto"
                        aria-labelledby="range-slider"
                        getAriaValueText={sliderValuetext}
                        min={100}
                        max={1000}
                        step={50}
                      />
                  </AccordionDetails>
                </Accordion>
              </div>
            </div>
            <div style={{display:'flex', flexDirection:'column', justifyContent:'space-evenly', paddingLeft:'5%', paddingRight:'5%'}}>
              <Button size="small" variant='contained' color='primary' onClick={resetSearch}>Clear Filters</Button>
              <Button size="small" variant='contained' color='secondary' onClick={resetSearch} style={{marginTop:'2%'}}>Clear Search</Button>
            </div>
          </div>
        </div>
    )
}

export default Search
