import {Redirect} from 'react-router-dom'

import { makeStyles } from '@material-ui/core/styles';
import { Card, CardActionArea, CardActions, CardContent, CardMedia, Button, Typography, IconButton } from '@material-ui/core';

import Bookmark from '@material-ui/icons/Bookmark'
import BookmarkBorder from '@material-ui/icons/BookmarkBorder'

import Post from '../components/Post'

const useStyles = makeStyles({
    root: {
      //maxWidth: 345,
      display: 'flex',
      flexDirection: 'column',
      alignItems: 'center',
      marginLeft:'88px',
    },
    media: {
      height: 140,
    },
  });

const Details = (props) => {
    console.log(props)
    const units = props.location.units;
    const classes = useStyles();

    if(typeof units === 'undefined'){
        return (
            <Redirect to='/mil-db/list' />
        )
    }

    console.log(units.unit);
    const unit = units.unit

    return (
        <div style={{display:"flex", flexDirection: 'column', justifyContent:'center', paddingTop:'80px', marginRight:'16px'}}>
            <Card className={classes.root}>
                <CardActionArea>
                    <CardMedia
                        className={classes.media}
                        image={unit.image}
                        title={unit.name}
                    />
                    <CardContent>
                    <Typography gutterBottom variant="h5" component="h2">
                        {unit.name}
                    </Typography>
                    <Typography variant="body2" color="textSecondary" component="p">
                        {unit.desc}
                    </Typography>
                    </CardContent>
                </CardActionArea>
                <CardActions>
                    <Button size="small" color="primary">
                    Share
                    </Button>
                    <Button size="small" color="primary">
                    Learn More
                    </Button>
                    <IconButton edge="end" aria-label="comments" onClick={() => props.location.func.onToggle(unit.id)}>
                        {unit.bookmarked? <BookmarkBorder /> : <Bookmark /> }
                    </IconButton>
                </CardActions>
            </Card>

            <Post title="abc" posts={[{a:"a"}]} />
        </div>
    )
}

export default Details
