import { BrowserRouter as Router, Route} from 'react-router-dom'
import { useState, useEffect } from 'react'
import { makeStyles } from '@material-ui/core/styles';
import './App.css'

import List from './view/List'
import Details from './view/Details'
import Settings from './view/Settings'

import Header from './components/Header'
import Footer from './components/Footer'

function App() {
  const [units, setUnits] = useState([]);
  const itemsPerPage = 10;
  const [page, setPage] = useState(1)
  const [numOfPages, setNumOfPages] = useState()
  const [isLoading, setLoading] = useState(true);

  useEffect(() => {
    const getUnits = async () =>{
      setLoading(true);
      const unitsFromServer = await fetchUnits()
      setUnits(unitsFromServer)
      setNumOfPages(Math.ceil(unitsFromServer.length / itemsPerPage));
      setLoading(false);
    }
    getUnits()
  }, []);

  //Fetch Units
  const fetchUnits = async() => {
    const res = await fetch('http://localhost:5000/listall')
    return await res.json()
  }

  const search = async(query) => {
    const res = await fetch(`http://localhost:5000/search?value=${query}`),
          data = await res.json();

    setUnits(data);
  }

  const filterType = async(type) => {
    const res = await fetch(),
          data = await res.json();

    setUnits(data);
  }

  const handlePaging = (event, page) => {
    setPage(page);
  }

  const toggleBookmark = (id) =>{
    console.log(id);
    setUnits(units.map((unit) => unit._id === id ? {...unit, bookmarked: !unit.bookmarked} : unit))
  }

  return (
    <Router>
        <div >
            <Header onSearch={search} />
            <div>
              <Route path='/mil-db/' exact></Route>
              <Route path='/mil-db/list' render={() => (<List units={units} onToggle={toggleBookmark} page={page} numOfPages={numOfPages} itemsPerPage={itemsPerPage} handlePaging={handlePaging} isLoading={isLoading} />)}></Route>
              <Route path='/mil-db/details' component={Details}></Route>
              <Route path='/mil-db/settings' component={Settings}></Route>
            </div>
            <Footer />
        </div>
    </Router>
  );
}

export default App;
